body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    background: url(/static/media/galaxies.f4745f64.JPG);
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    max-width: 2200px;
    max-height: 1600px;
    min-width: 720px;
    min-height: 1280px;
    background-size: auto;
    position: relative;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: auto;
    justify-content: center;
}

html, body {
    height: 100%;
    overflow: hidden;
}



#text {
    margin-right: 40%;
    margin-left: 25%;
    display: inline-block;
    position: absolute;
}

h3 {
    font-size: 2.00em;
    font-family: Tahoma;
    background-color: slategrey;
    color: black;
    padding: 3%;
    border-radius: 5%;
    text-align: center;
}
h2 {
    font-size: 2.00em;
    font-family: Tahoma;
    background-color: coral;
    color: black;
    padding: 3%;
    border-radius: 5%;
    text-align: center;
}

.MilesContainer {
    padding-right: 15%;
    padding-top: 25%;
    display: inline-block;
    position: absolute;
    margin-left: 15%;
}
